import React from 'react';
import PropTypes from 'prop-types';
import Helmet from 'react-helmet';
import { StaticQuery, graphql, withPrefix } from 'gatsby';

const detailsQuery = graphql`
  query DefaultSEOQuery {
    site {
      siteMetadata {
        title
        description
        author
      }
    }
  }
`;

/*
<meta httpEquiv="Content-Type" content="text/html; charset=UTF-8" />
<meta httpEquiv="Access-Control-Allow-Origin" content="*" />
<meta httpEquiv="Access-Control-Allow-Headers" content="*" />
<meta httpEquiv="Access-Control-Expose-Headers" content="*" />
<meta httpEquiv="Access-Control-Allow-Credentials" content="true" />
<meta httpEquiv="X-UA-Compatible" content="IE=edge" />
<meta id="viewport" name="viewport" content="width=device-width,
user-scalable=no, initial-scale=1, minimum-scale=1, maximum-scale=1" />
 */

function SEO({ description, lang, meta, image, keywords, title }) {
  return (
    <StaticQuery
      query={detailsQuery}
      render={(data) => {
        const metaDescription = description || data.site.siteMetadata.description;
        return (
          <Helmet
            htmlAttributes={{ lang }}
            title={title}
            titleTemplate={`%s | ${data.site.siteMetadata.title}`}
            meta={[
              { name: 'description', content: metaDescription },
              { property: 'og:type', content: 'website' },
              { property: 'og:title', content: title },
              { property: 'og:description', content: metaDescription },
              { property: 'og:image', content: image },
              { name: 'twitter:card', content: 'summary_large_image' },
              { name: 'twitter:creator', content: data.site.siteMetadata.author },
              { name: 'twitter:title', content: title },
              { name: 'twitter:description', content: metaDescription },
              { name: 'twitter:image', content: image },
            ]
              .concat(
                keywords.length > 0
                  ? { name: 'keywords', content: keywords.join(', ') }
                  : []
              )
              .concat(meta)}
          />
        );
      }}
    />
  );
}

SEO.defaultProps = {
  lang: 'en',
  meta: [],
  keywords: [],
  image: withPrefix('images/banner.png'),
  description: null,
};

SEO.propTypes = {
  description: PropTypes.string,
  lang: PropTypes.string,
  image: PropTypes.string,
  meta: PropTypes.arrayOf(PropTypes.string),
  keywords: PropTypes.arrayOf(PropTypes.string),
  title: PropTypes.string.isRequired,
};

export default SEO;
