import React from 'react';
import PropTypes from 'prop-types';
import styled from '@emotion/styled';

export const Container = ({ height, innerWidth, children, ...props }) => (
  <div css={{ height }}>
    <div
      css={{
        width: innerWidth,
        height: 'inherit',
        margin: '0 auto',
      }}
      {...props}
    >
      { children }
    </div>
  </div>
);

Container.propTypes = {
  height: PropTypes.string.isRequired,
  innerWidth: PropTypes.string,
};

Container.defaultProps = {
  innerWidth: '1000px',
};

export const HorizontalList = styled.nav`
  margin: 0;
`;

export const ListItem = styled.li`
  display: inline-block;
  margin: 0;
  margin-left: ${props => props.gap};  
  
  :first-of-type {
    margin-left: 0;
  }
`;
