import React from 'react';
import styled from '@emotion/styled';
import { css } from '@emotion/core';
import { graphql } from 'gatsby';

import { Container } from '../components/commons';
import Layout from '../components/layout';
import Preview from '../components/preview';
import Bio from '../components/bio';
import SEO from '../components/SEO';

const ContainerStyle = css`
  display: flex;
  margin-top: 24px;
`;

const ArticleSection = styled.section`
  flex: 7;
`;

const SidebarSection = styled.section`
  flex: 3;
  margin-left: 64px;
`;

function BlogIndex({ data, location }) {
  const siteTitle = data.site.siteMetadata.title;
  const siteDescription = data.site.siteMetadata.description;
  const posts = data.allMarkdownRemark.edges;

  return ( // description, lang, meta, image, keywords, title
    <Layout location={location} title={siteTitle}>
      <SEO
        title={siteTitle}
        description={siteDescription}
        image="images/banner.png"
      />
      <Container css={ContainerStyle} innerWidth="1000px" height="inherit">
        <ArticleSection>
          {posts.map(({ node }, index) => (
            <Preview
              css={{ marginTop: index > 0 ? '40px' : 0 }}
              key={node.fields.slug}
              title={node.frontmatter.title || node.fields.slug}
              url={node.fields.slug}
              category={node.frontmatter.category}
              summary={node.frontmatter.description || node.excerpt}
              date={node.frontmatter.date}
              cover={node.frontmatter.cover}
            />
          ))}
        </ArticleSection>
        <SidebarSection>
          <Bio />
        </SidebarSection>
      </Container>
    </Layout>
  );
}

export default BlogIndex;

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
        description
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            title
            description
            category
            tags
            cover {
              childImageSharp {
                resolutions(height: 320) {
                  src
                }
              }
            }
          }
        }
      }
    }
  }
`;
